import React from 'react';
import {pw_type, pw_strength, combinations, entropy, totaltime} from './Analyzer';
import {Guideline, Problems, expo, financial, checkString} from './Misc';
import {generatePassword, Generator} from './Generator';
import {issues} from './issues';
import {makeDict} from './dict';
import {interpolateRgb, quantize} from "d3-interpolate";
import {buildStrength} from './strength';
import {Info} from './info';
import {Compare} from './compare';
import axios from 'axios';
import {Navbar, Nav} from 'react-bootstrap';

import './App.css';
import './Generator.css';
import './strength.css';
import './Guidelines.css';
import './compare.css';
import './info.css';
import './footer.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import './bootstrap.scss';
// Cracks per second.
const CPS = 588235;
var mql = null;
// Word lookup object.
var dict = {};
// Object of letter arrays.
var alphadict = {}

const Header = props => (
  <header>
    <Navbar expand="lg" sticky="top" id="header">
    <Navbar.Brand onClick={props.onclick} className="logo">PASSCHECKER</Navbar.Brand>
    <Navbar.Toggle aria-controls="basic-navbar-nav"/> 
    <Navbar.Collapse id="basic-navbar-nav">
      <Nav className="mr-auto">
        <Nav.Link className="nav-element" onClick={props.onclick}>Info</Nav.Link>
        <Nav.Link className="nav-element" onClick={props.onclick}>Guidelines</Nav.Link>
        <Nav.Link className="nav-element" onClick={props.onclick}>Strength</Nav.Link>
        <Nav.Link className="nav-element" onClick={props.onclick}>Generator</Nav.Link>
        <Nav.Link className="nav-element" onClick={props.onclick}>Compare</Nav.Link>
      </Nav>
    </Navbar.Collapse>
    </Navbar>
  </header>
)

const Footer = props => (
  <footer style={props.style} className="Center">
    <div className="footer">Gavin Gosling - 2020</div>
    <div className="footer"><a href="https://gavingosling.me">Website</a></div>
  </footer>
)

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      password  : "",
      generated : "",
      hide      : 0,
      len       : 16,
      genSelect : "0",
      page      : 4,
      top       : {backgroundColor: "grey"},
      bottom    : {backgroundColor: "grey"}
    }

    this.handleChangeStrength = this.handleChangeStrength.bind(this);
    this.handlePageChange = this.handlePageChange.bind(this);
    this.handleClickGenerator = this.handleClickGenerator.bind(this);
    this.handleLengthChange = this.handleLengthChange.bind(this);
    this.handleTypeChange = this.handleTypeChange.bind(this);
    this.handleClickHide = this.handleClickHide.bind(this);
    this.handleChangeCompare = this.handleChangeCompare.bind(this)
  }

  componentDidMount() {
    // Get the dictionary
    axios.get("http://gavingosling.me/data/dictionary.txt")
      .then(response => {
        let result = null;
        result = makeDict(response.data);
        dict = result.dict;
        alphadict = result.alphadict;
      })
      .catch(error => {
        console.log(error);
      })
  }

  render(){
    let page = this.state.page;
    let body = null;
    let footerstyle = {
      position: "fixed",
      left: "0",
      bottom: "0",
      width: "100%",
      background: "#282c34",
      color: "white",
      outline: "4px solid #F28E2B"
    };
    if(page == 0){
      let value = this.state.password;
      body = buildStrength(value, this.handleChangeStrength, this.state.hide, this.handleClickHide);
    }

    if(page == 1){
      body = <Generator selectonchange = {this.handleTypeChange} 
                        onclick={this.handleClickGenerator} 
                        generated={this.state.generated} 
                        onchange={this.handleLengthChange} 
                        len={this.state.len}>
             </Generator>
    }

    if(page == 2){
      body = <Compare onchange={this.handleChangeCompare} top={this.state.top} bottom={this.state.bottom}></Compare>
    }

    if(page == 3){
      let mq = window.matchMedia("screen and (max-width: 700px)");
      if (mq.matches) {
        footerstyle["position"] = "static";
      } 
      body = <div>
              <Guideline></Guideline>
              <Problems></Problems>
            </div>
    }
    
    if(page == 4){
      body = <Info onclick={this.handlePageChange}></Info>
      footerstyle = {
        display: "static",
        background: "#282c34",
        color: "white",
        outline: "4px solid #F28E2B"
      }
    }

    return (
      <div className="App">
        <Header onclick={this.handlePageChange}></Header>
        {body}
        <Footer style={footerstyle}></Footer>
      </div>
    );
  }

  handleChangeStrength(e) {
    var val = e.target.value;
    if(val.length > 26){
      val = val.substring(0, 26);
      e.target.value = val;
    }
    const value = analyzePassword(val);
    this.setState({
      ...this.state,
      password: value
    });
  }

  handleChangeCompare(e){
    let top = document.getElementById("pass1").value;
    let bottom = document.getElementById("pass2").value;
    let green = "#59A14F";
    let red = "#E15759";
    if(top != "" && bottom != ""){
      let val1 = top;
      if(val1.length > 26){
        val1 = val1.substring(0, 26);
      }
      val1 = analyzePassword(val1);
      let val2 = bottom;
      if(val2.length > 26){
        val2 = val2.substring(0, 26);
      }
      val2 = analyzePassword(val2);
      let result = comparePass(val1, val2);

      console.log(result);
      if(result == 1){
        top = {backgroundColor: red}
        bottom = {backgroundColor: green}
      } else if(result == -1){
        top = {backgroundColor: green}
        bottom = {backgroundColor: red}
      } else {
        top = {backgroundColor: "gray"}
        bottom = {backgroundColor: "gray"}
      }
      this.setState({
        ...this.state,
        top: top,
        bottom: bottom
      });
   } else {
      top = {backgroundColor: "gray"}
      bottom = {backgroundColor: "gray"}
      this.setState({
        ...this.state,
        top: top,
        bottom: bottom
      });
   }
  }

  handleClickHide(){
    var x = document.getElementById("inputPass");
    let val = null;
    if (x.type === "password") {
      val = "text";
    } else {
      val = "password";
    }
    this.setState({
      ...this.state,
      hide: val
    });
  }

  handleClickGenerator(){
    let pass = "";
    switch(this.state.genSelect){
      case "0":
        pass = generatePassword(1, this.state.len, dict, null);
        break;
      case "1":
        pass = generatePassword(2, this.state.len, dict, null);
        break;
      default:
        console.log("Case not found.")
        break;
    }
    this.setState({
      ...this.state,
      generated: pass
    });
  }

  handleLengthChange(e){
    let val = document.getElementById("lenslide").value;
    this.setState({
      ...this.state,
      len: val
    });
  }

  handleTypeChange(e){
    let val = document.getElementById("genSelect").value;
    console.log(val);
    this.setState({
      ...this.state,
      genSelect: val
    });
  }

  /*
  case "3":
        if(/^[a-z]+$/i.test(input.value)){
          pass = generatePassword(3, this.state.len, alphadict, input.value);
        } else {
          console.log("ERROR, ONLY LETTERS ALLOWED")
        }
  */

  handlePageChange(e){
    const val = e.target.innerText;
    var page = null;
    switch(val){
      case "PassChecker":
        page = 4;
      case "Info":
        page = 4;
      case "Welcome to PassChecker!":
        page = 4;
        break;
      case "Password Strength":
        page = 0;
      case "Strength":
        page = 0;
        break;
      case "Generator":
        page = 1;
      case "Generate Password":
        page = 1;
        break;
      case "Compare":
        page = 2;
      case "Compare Passwords":
        page = 2;
        break;
      case "Guidelines":
        page = 3;
        break;
      default:
        page = 4;
        break;
    }
    this.setState({
      ...this.state,
      page: page
    });
  }
}


// Sort passwords base on length
function comparePass(a, b){
  console.log(a)
  console.log(b)
  if(Number(a.bits) < Number(b.bits)){
    return 1;
  }
  if(Number(a.bits) > Number(b.bits)){
    return -1;
  }
  return 0;
}

// Analyze Password
function analyzePassword(e){
  if(e != null){
    let text = e;
    if(checkString(text)){
      let charpool = pw_type(text);
      let length = text.length;
      let bits = entropy(length, charpool.amount);
      let comb = combinations(length, charpool.amount);
      let crack = cracktime(comb);
      crack = totaltime(crack);
      let obj = pw_strength(bits);
      //let style = addPassword(obj.value);
      bits = financial(bits);
      let issue = issues(text, charpool.types, length, dict);
      return {name: text, strength: obj.strength, cracktime: crack, bits:bits, issues: issue};
    }
  }
}

// Password Hue
function passHue(value){
  let colors = quantize(interpolateRgb("red", "green"), 5);
  return colors[value]
}

// Passbar style
function addPassword(value){
  let percentage = 25*(value+1);
  let hue = passHue(value);
  let style = {
    width: percentage + "%",
    background: hue
  }
  return style;
}

// Amount of time in seconds until crack.
function cracktime(comb){
  return comb/CPS;
}



export default App;
