import React from 'react';

export const Info = props => (
  <div className="Center">
    <div className="infoCenter"> 
      <h1 onClick={props.onclick} className="infoTitle">Welcome to PassChecker!</h1>
      <div className="infoDesc"> This site contains various password related information and functionality. </div>
    </div>
    <div className="infoCenter">
      <h1 onClick={props.onclick} className="infoTitle">Guidelines</h1>
      <div className="infoDesc">This page outlines strategies for creating good passwords as well as typical problems passwords can have.</div>
    </div>
    <div className="infoCenter">
      <h1 onClick={props.onclick} className="infoTitle">Password Strength</h1>
      <div className="infoDesc">This page checks how a strong a given password is. It is also able to give recommendations base on the given password.</div>
    </div>
    <div className="infoCenter">
      <h1 onClick={props.onclick} className="infoTitle">Generate Password</h1>
      <div className="infoDesc">This page is used to generate password by using a sequence of words or characters.</div>
    </div>
    <div className="infoCenter">
      <h1 onClick={props.onclick} className="infoTitle">Compare Passwords</h1>
      <div className="infoDesc">This page is used to compare two passwords</div>
    </div>
  </div>
)