import React from 'react';
export const Compare = props => (
    <div className="Center">
      <h2 className="strtitle">COMPARE PASSWORDS</h2>
      <div className="compareouterdiv">
        <div className="comparediv" style={props.top}>
          <input onChange={props.onchange} className="compareinput" id="pass1" type="text" placeholder="ENTER PASSWORD"></input>
        </div>
        <div className="comparediv" style={props.bottom}>
          <input onChange={props.onchange} className="compareinput" id="pass2" type="text" placeholder="ENTER PASSWORD"></input>
        </div>
      </div>
    </div>
  )